@media (min-width: 768px) {
    .todoRight {
        max-width: 750px;
        margin: 50px auto 130px;
        padding: 50px 30px;
        border-radius: 10px;
        border: 1px solid rgba(255, 255, 255, 0.5);
    }
}
@media (min-width: 1200px) {
    .todoRight {
        width: 100%;
        max-width: none;
        margin: 0;
    }
}